export default {
	data() {
		return {
			png: "" //图片
		}
	},
	methods: {
		getHomeSlideFunc() {
			var _this = this;
			uni.request({
				url: 'http://ceshi3.dishait.cn/api/goods/hotlist', //请求接口
				header: {
					'content-type': 'application/x-www-form-urlencoded', //自定义请求头信息
				},
				success: (res) => { //请求成功后返回
					// console.log(res)
					_this.png = res.data.data
					// console.log(_this.png)
				}
			});
		}
	},
	onLoad: function(option) {
		this.getHomeSlideFunc()
	}
}
